import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import styles from "../pages/about-css-module.module.css"

const Title = props => (
  <div className={styles.title}>
    <h2 className={styles.title}>{props.title}</h2>
  </div>
)

export default ({ data }) => (
  <Layout>
    <h1 title="The title">The title</h1>
    <div>
      Gatsby를 실습중입니다. <br /> 튜토리얼을 참고해서 만들어보기도 하고,
      이것저것 자유롭게 실험을 해보고 있습니다.
      <p><br /></p>
    </div>

    <div>
      This site is made for the practice of Gatsby. <br />I am learning to
      follow the tutorial and learn to experiment freely.
    </div>
  </Layout>
)

//page query
export const query = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
  }
`
